<template>
  <div class="spreadtask">
      <div class="top">
        <el-row :gutter="20">
            <el-col :span="16">
                <div class="grid-content bg-purple">
                    <span style="font-size: 14px;">:</span>
                     <el-date-picker
                     size="small"
                    v-model="picktime"
                    type="datetimerange"
                    range-separator=""
                    start-placeholder=""
                    end-placeholder="">
                    </el-date-picker>
                    <el-select v-model="station" placeholder="" size="small"  style="width: 240px;">
                        <el-option
                        v-for="item in stationList"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                        </el-option>
                    </el-select>
                    <a href="javascript:;" class="addproduct"><i></i></a>
                </div>
            </el-col>
            <el-col :span="8">
                <div class="grid-content bg-purple" style="float: right;">
                    <el-input v-model="valueSeach" placeholder="" size="small" style="width: 240px;"></el-input>
                    <el-button style="padding: 8px 20px;">  </el-button>
                </div>
            </el-col>
        </el-row>
          <div class="stateSelect">
              <span> : </span>
                <el-radio-group v-model="spreadState">
                    <el-radio-button label=""></el-radio-button>
                    <el-radio-button label="20"></el-radio-button>
                    <el-radio-button label="10"></el-radio-button>
                    <el-radio-button label=" 20"></el-radio-button>
                    <el-radio-button label=" 20"></el-radio-button>
                </el-radio-group>
          </div>
      </div>
       <div class="listWrap">
            <el-table
            :data="tableData"
            style="width: 100%">
            <el-table-column
            label="ID"
            width="200"
            prop="productID">
            </el-table-column>
            <el-table-column
            label=""
            width="180"
            prop="tasktype">
            </el-table-column>
            <el-table-column
            label=""
            prop="taskmessage">
            </el-table-column>
            <el-table-column
            label=""
            width="180"
            >
                <template slot-scope="scope">
                <div  v-if="scope.row.isShow === false">
                <i class="jtwrap"></i><span class="ordernamewrap">{{scope.row.goingstate}}</span>
                </div>
                <el-popover trigger="hover" placement="top" v-else>
                    <p style="color: #fe6c6f;">{{ scope.row.tiktip }}</p>
                    <div slot="reference" class="name-wrapper">
                        <i class="jtwrap"><img src="./jth.png" alt=""></i><span class="ordernamewrap">{{scope.row.goingstate}}</span>
                    </div>
                </el-popover>
            </template>
            </el-table-column>
            <el-table-column label="">
            <template slot-scope="scope">
                <el-button
                type="primary"
                size="mini"
                ></el-button>
                <el-button
                size="mini"
                type="plain"></el-button>
            </template>
            </el-table-column>
        </el-table>
    </div>
    <div class="paginationwrap">
      <el-pagination
        background
        layout="prev, pager, next"
        :total="1000">
        </el-pagination>
      </div>
  </div>
</template>
<script>
export default {
    components: { },
    props: {
     
    },
    data(){
        return{
            picktime: [new Date(2000, 10, 10, 10, 10), new Date(2000, 10, 11, 10, 10)],
            station: '',
            stationList: [
                {
                    value: 'New York',
                    label: 'New York'
                },
                {
                    value: 'London',
                    label: 'London'
                },
                {
                    value: 'Sydney',
                    label: 'Sydney'
                }
            ],
            valueSeach:'' ,
            spreadState:'',  
            tableData: [{
            isShow:false,
            productID: '',
            tasktype: '',
            taskmessage: ': : : :',
            goingstate: '',
            tiktip:''
            }, {
            isShow:false,
            productID: '',
            tasktype: '',
            taskmessage: ': : : :',
            goingstate: '',
            tiktip:''
            }, {
            isShow:true,
            productID: '',
            tasktype: '',
            taskmessage: ': : : :',
            goingstate: '',
            tiktip:''
            }, {
            isShow:false,
            productID: '',
            tasktype: '',
            taskmessage: ': : : :',
            goingstate: '',
            tiktip:''
            }]
        }
    },
    methods: {    
     
      }    
}
</script>
<style lang="scss">
.spreadtask{
    background-color: #ffffff;
    padding: 15px 20px 35px;
    border: 1px solid #cccccc;
    border-top: none;
   min-height: 400px;
    overflow: hidden;
      .top{
         white-space: nowrap;
         .stateSelect{
             margin: 15px 0px;
             padding: 5px 8px;
             font-size: 16px;
             border: 1px solid #e6e6e6;
            .el-radio-button__orig-radio:checked+.el-radio-button__inner{
                background-color: #FFF;
                color: #205081; 
                font-weight: 600;
                border-color: #ffffff;
            }
            .el-radio-button__inner{
                border: 1px solid #fff;
            }
         }
     }
    .listWrap{
         border: 1px solid #e6e6e6;
         .el-table th:first-child + th+th+th > .cell{
                 left: 20px;
         }
         .jtwrap{
             display: inline-block;
             width: 16px;
             height: 16px;
             overflow: hidden;
             vertical-align: top;
             margin-top: 3px;
             margin-right: 5px;
             img{
                 width: 100%;
                 height: 100%;
             }
         }
         .ordernamewrap{
             display: inline-block;
             vertical-align: top;
             height: 16px;
         }
     }
    .paginationwrap{
        margin-top: 20px;
        text-align: right;
    }
}
</style>




