<template>
  <div class="alipay-pay">
    <el-row type="flex" justify="center">
      <div class="package-box" style="padding-top: 20px;">
        :
        <span style="margin-left: 30px;">
          <el-radio v-model="form.amount" label="50">50</el-radio>
          <el-radio v-model="form.amount" label="100">100</el-radio>
          <el-radio v-model="form.amount" label="200">200</el-radio>
          <el-radio v-model="form.amount" label="500">500</el-radio>
          <el-radio v-model="form.amount" label="1000">1000</el-radio>
        </span>
      </div>
    </el-row>
    <el-row type="flex">
      <div class="" style="padding-top: 50px; padding-left:480px;">
        <el-radio v-model="form.amount" label="0"><el-input style="margin-left:20px;" v-model="form.amount"></el-input></el-radio>
      </div>
    </el-row>
    <el-row type="flex" justify="center">
      <div class="button-box" style="padding-top: 50px;">
        <el-button class="success" @click="onSubmit"></el-button>
        <el-button ></el-button>
      </div>
    </el-row>

    <el-row type="flex" justify="center">
      <el-col :span="15">
        <div class="text-info-box">
          <p>:</p>
          <p>1</p>
          <p>2</p>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import {callApiForMbs} from '@/data/callApi'
export default {
  data () {
    return {
      form: {
        payerAccount: '',
        payeeAccount: '',
        financeItemCode: '1',
        tradingType:'5',
        amount: '50',
        transactionNumber:'',
      }
    }
  },
  methods: {
    onSubmit () {
      let me = this
      callApiForMbs('/finance/recharge_amount', this.form, function (res) {
        if (res.status >= 200 && status < 300) {
          if (res.data.success) {
            me.$message({message: '', type: 'success'})
            return
          }
        }
        me.$message.error('')
      })
    }
  }
}
</script>

<style scoped lang="scss">
  .alipay-pay {
    .text-info-box {
      padding-top: 20px;
      font-size: 10px;
    }
    .el-button {
      height: 38px;
    }
    .success {
      background-color: #205081;
      color: white;
    }
  }
</style>



