<template>
  <div class="onlinebank-pay">
    <el-row type="flex" justify="center">
      <div class="package-box" style="padding-top: 20px;">
        :
        <span style="margin-left: 30px;">
          <el-radio v-model="form.amount" label="50">50</el-radio>
          <el-radio v-model="form.amount" label="100">100</el-radio>
          <el-radio v-model="form.amount" label="200">200</el-radio>
          <el-radio v-model="form.amount" label="500">500</el-radio>
          <el-radio v-model="form.amount" label="1000">1000</el-radio>
        </span>
      </div>
    </el-row>
    <el-row type="flex">
      <div class="" style="padding-top: 50px; padding-left: 480px;">
        <el-radio v-model="form.amount" label="0"><el-input style="margin-left: 20px;" v-model="radio"></el-input></el-radio>
      </div>
    </el-row>

    <el-row >
      <!--  -->

      <p style="padding-top: 20px;padding-left: 510px;">
        <span></span>
        <el-input placeholder="" style="width: 200px;" v-model="form.payerAccount"></el-input>
      </p>

      <p style="padding-top: 20px;padding-left: 510px;">
        <span></span>
        <el-input placeholder="" style="width: 200px;" v-model="form.payeeAccount"></el-input>
      </p>

      <p style="padding-top: 20px;padding-left: 510px;">
        <span></span>
        <el-input placeholder="" style="width: 200px;" v-model="form.transactionNumber"></el-input>
      </p>
    </el-row>

    <el-row type="flex" justify="center">
      <div class="button-box" style="padding-top: 50px;">
        <el-button style="background-color: #205081; color: white;" @click="onSubmit"></el-button>
        <el-button ></el-button>
      </div>
    </el-row>

    <el-row type="flex" justify="center">
      <el-col :span="15">
        <div class="text-info-box" style="padding-top: 50px;">
          <p ></p>
          <p >3</p>
        </div>
      </el-col>

    </el-row>
  </div>
</template>

<script>
import {callApiForMbs} from '@/data/callApi'
export default {
  data () {
    return {
      radio: '',
      form: {
        payerAccount: '',
        payeeAccount: '',
        amount: '50',
        financeItemCode:'2',
        tradingType: 3,
        transactionNumber: '',
        notes: ''
      }
    }
  },
  methods: {
    onSubmit () {
      let me = this
      callApiForMbs('/finance/recharge_amount', this.form, function (res) {
        console.log(res)
        if (res.status >= 200 && res.status < 300) {
          if (res.data.success) {
            console.log('financeItemCode')
            me.$message({message: res.data.content.content.msg, type: 'success'})
            return
          }
        }
        me.$message.error(res.data.errmsg)
      })
    }
  }
}
</script>

<style scoped lang="scss">
.onlinebank-pay {
  .text-info-box {
    padding-top: 20px;
    font-size: 10px;
  }
  .el-button {
    height: 38px;
  }
  .success {
    background-color: #205081;
    color: white;
  }
}
</style>



