<template>
  <div class="ManageRole">
    <el-container>
      <el-header style="height: 40px;">
        <div class="top-menu">
          <li @click="routerGo('/user/BasicInfo')"></li>
          <li @click="routerGo('/user/ManageStaff')"></li>
          <li @click="routerGo('/user/ManageRole')" class="is-active"></li>
        </div>
      </el-header>
      <el-main>
        <el-row class="row-bg">
          <el-col :span="24">
            <div class="main-header">
              <div class="left-operation">
                <el-button type="small" style="background-color: #67C238;color: white;" @click="openAddRoleDialog()">+</el-button>
              </div>
              <div class="right-operation">
                <el-autocomplete v-model="queryValue" :fetch-suggestions="querySearchAsync" size="small" suffix-icon="el-icon-search"
                  placeholder="" @select="handleSelect"></el-autocomplete>
              </div>
            </div>
          </el-col>
        </el-row>
        <!--  -->
        <el-row class="row-bg">
          <div class="table-data-area">
            <el-table :data="tableData" size="small" header-row-class-name="table-title">
              <el-table-column prop="name" label=""></el-table-column>
              <el-table-column prop="content" label=""></el-table-column>
              <el-table-column label="" width="170px">
                <template slot-scope="scope">
                  <el-button size="small" @click="openAddRoleDialog(scope.row)"></el-button>
                  <el-button style="background-color: #FCA941; color: white;" size="small" @click="deleteDialog(scope.row)"></el-button>
                </template>
              </el-table-column>
            </el-table>
          </div>
        </el-row>
        <el-row class="row-bg footer-row">
          <el-col :span="24">
            <div class="left-export-button">
              <el-button size="small"></el-button>
            </div>
            <div class="current-page-area">
              <el-pagination
                class="right"
                background
                layout="prev, pager, next"
                :total="1000">
              </el-pagination>
            </div>
          </el-col>
        </el-row>
        <el-dialog title="" :visible.sync="addFlag" width="40%" :before-close="closeDialog">
          <el-form :model="form">
            <el-form-item label="" >
              <el-input v-model="form.name" style="width: 250px;"></el-input>
            </el-form-item>
            <el-form-item label="" >
              <el-input v-model="form.content" style="width: 250px;"></el-input>
            </el-form-item>
            <el-form-item >

              <!--  -->
              <el-tabs type="border-card" @tab-click="changeMenuItem">
                <el-tab-pane v-for="item in authItem" :label="item.name" :name="item.id + ''">
                  <span style="margin-right: 20px;" v-for="childItem in childAuthItem"><el-checkbox v-model="childItem.checked">{{childItem.name}}</el-checkbox></span>
                </el-tab-pane>
              </el-tabs>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="closeDialog"> </el-button>
            <el-button type="primary" @click="commitAddRole"> </el-button>
          </div>
        </el-dialog>
        <el-dialog title="" :visible.sync="removeRole" width="30%">
          <p>:</p>
          <h4>{{form.roleName}}</h4>
          <span>{{form.roleDescribe}}</span>
          <div slot="footer" class="dialog-footer">
            <el-button @click="closeDialog"> </el-button>
            <el-button type="primary" @click="commitDeleteRole"> </el-button>
          </div>
        </el-dialog>
      </el-main>
    </el-container>
  </div>
</template>

<script>
import {callApiToken} from '@/data/callApi'
export default {
  name: 'ManageRole',
  data () {
    return {
      form: {
        id: 0,
        name: '',
    		applicationId: 1,
    		auths:[]
      },
      authItem: [],
      allAuthItem: [],
      childAuthItem: [],

      checked: [],

      addFlag: false,
      removeRole: false,
      activeIndex: '/managerole',
      queryValue: '',
      currentPage: 1,
      dateValue: '',
      options: [
        {label: '', value: '1'},
        {label: '', value: '2'},
        {label: '', value: '3'},
        {label: '', value: '4'},
        {label: '', value: '5'},
        {label: '', value: '6'},
        {label: '', value: '7'}
      ],
      selectValue: '',
      tableData: [
        {Id: '1', name: '', content: ''}
      ]
    }
  },
  methods: {
    querySearchAsync () {

    },
    handleSelect () {

    },
    handleSizeChange () {

    },
    handleCurrentChange () {

    },
    handleAvatarSuccess () {

    },
    routerGo (url) {
      this.$router.push({path: url})
    },
    changeMenuItem (val) {
      this.childAuthItem = []
      // console.log(this.allAuthItem)
      for (var i = 0; i < this.allAuthItem.length; i++) {
        if (this.allAuthItem[i].parentId == val.name) {
          this.childAuthItem.push(this.allAuthItem[i])
        }
        // console.log(this.allAuthItem[i].parentId == val.name)
      }
      console.log(this.childAuthItem)
      // console.log(val.name)
    },
    // 
    openAddRoleDialog (rowValue) {
      if (rowValue) {
        this.form.id = rowValue.id
        this.form.name = rowValue.name
        this.form.content = rowValue.content
        // callApiToken('')
      }
      this.addFlag = true;
    },
    commitAddRole () {
      var temp = {
        name: this.form.name,
        content: this.form.content
      }
      if (this.form.id != 0) {
        temp.id = this.form.id
      }
      // callApiToken('/role/save_role',temp, function (res) {

      // })
      this.addFlag = false
    },
    deleteDialog (rowValue) {
      this.form.Id = rowValue.Id
      this.form.roleName = rowValue.roleName
      this.form.roleDescribe = rowValue.roleDescribe
      this.removeRole = true
    },
    commitDeleteRole () {
      var temp = []
      for (var i = 0; i < this.tableData.length; i++) {
        if (this.tableData[i].Id === this.form.Id)
          continue
        temp.push(this.tableData[i])
      }
      this.tableData = temp
      this.removeRole = false
    },
    closeDialog () {
      this.form.Id = ''
      this.form.roleName = ''
      this.form.roleDescribe = ''
      this.addFlag = false
      this.removeRole = false
    },
    updateTableData(res){
      console.log(res)
      if (res.status >= 200 && res.status < 300) {
        if (res.data.success) {
          this.tableData = res.data.content.records
        }
      }
    }
  },
  created: function () {
    let me = this
    // 
    callApiToken('/auth/query_auths_by_application', {applicationId: 1}, function (res) {
      console.log(res)
      if (res.data.success) {
        me.allAuthItem = res.data.content
        for (var i = 0; i < res.data.content.length; i++) {
          if (res.data.content[i].parentId === null) {
            me.authItem.push(res.data.content[i])
          }
        }
      }
    })

  }
}
</script>

<style scoped lang='scss'>
  .ManageRole {
    .top-menu {
      height: 38px;
      li {
        background-color: white;
        border: 1px solid #d4d4d4;
        height: 38px;
        width: 110px;
        float: left;
        line-height: 38px;
        text-align: center;
        list-style-type:none;
      }
      .is-active {
        height: 36px;
        line-height: 37px;
        border-top: 3px solid #205081;
        border-bottom: 1px solid white;
      }
    }
    .el-main {
      padding-top: 0px;
      .row-bg {
        border-left: 1px solid #D7D7D7;
        border-right: 1px solid #D7D7D7;
        background-color: white;
        .main-header {
          padding-top: 20px;
          padding-left: 40px;
          padding-right: 40px;
          .left-operation {
            .datebox {
              width: 230px;
            }
            .selectbox {
              width: 120px;
            }
            float: left;
          }
          .right-operation {
            float: right;
          }
        }
        .table-data-area {
          padding-left: 40px;
          padding-right: 40px;
          padding-top: 20px;
        }
        .left-export-button {
          padding-top: 20px;
          padding-left: 40px;
          float: left;
          padding-bottom: 10px;
        }
        .current-page-area {
          padding-top: 20px;
          padding-right: 40px;
          float: right;
        }
      }
    }
    .footer-row {
      border-bottom: 1px solid #d7d7d7;
    }
  }
</style>



